.login-wrapper {
  height: 100%;
  position: relative;
  .adm-nav-bar-back {
    color: #fff;
  }
  .login-btn-wrapper {
    width: 400px;
    margin: 44px auto 44px;
    margin-top: 44px;
    .adm-capsule-tabs-header {
      border-bottom: none;
    }
    .adm-capsule-tabs-tab-active {
      color: #fff !important;
      font-size: 40px !important;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
    }
    .adm-capsule-tabs-tab {
      background-color: transparent;
      font-size: 32px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #5E6272;
    }
  }
  .adm-list-body {
    margin: 0 80px;
    border: none;
  }
  .login-item {
    display: flex;
    background: #181A20;
    align-items: center;
    padding-bottom: 40px;
    .login-item-title {
      display: inline-block;
      width: 100px;
      font-size: 28px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #FFFFFF;
      margin-right: 76px;
    }
    .login-item-component {
      flex: 1;
      display: flex;
      height: 100px;
      border-bottom: 1px solid;
    }
  }
  .adm-input-element {
    --color: #fff;
    --placeholder-color:  #5E6272;
    --font-size: 28px;
    padding-left: 5px;
  }
  .extraPart {
    border-left: solid 1px #eeeeee;
    padding-left: 12px;
    font-size: 17px;
    line-height: 22px;
  }
  .eye {
    padding: 4px;
    cursor: pointer;
    svg {
      display: block;
      font-size: var(--adm-font-size-7);
    }
  }
  .login-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 590px;
    height: 96px;
    background: #FD3636;
    border-radius: 48px;
    margin: 266px auto 32px; 
    font-size: 28px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #fff;
  }
  .forget-password {
    font-size: 28px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #FD3636;
    margin-left: 80px;
  }
}